<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-cascades"></i>大转盘
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>

        <div class="container">

            <el-table
                    :data="tableData"
                    border
                    class="table"
                    ref="multipleTable"
                    header-cell-class-name="table-header"
            >
                <el-table-column prop="Id" label="ID" width="55" align="center"></el-table-column>
                <el-table-column prop="Title" label="奖励内容"></el-table-column>
                <el-table-column prop="AwardType" label="奖励类型">
                    <template slot-scope="scope">{{scope.row.AwardType===1?'金币':'积分'}}</template>
                </el-table-column>
                <el-table-column prop="Gold" label="金额">
                </el-table-column>

                <el-table-column prop="RemainCount" label="剩余数">
                </el-table-column>
                <el-table-column label="操作" width="180" align="center">
                    <template slot-scope="scope">
                        <el-button
                                type="text"
                                icon="el-icon-edit"
                                @click="handleEdit(scope.$index, scope.row)"
                        >编辑</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>

        <!-- 编辑弹出框 -->
        <el-dialog title="编辑" :visible.sync="editVisible" width="30%">
            <el-form ref="form" :model="form" label-width="70px">
                <el-form-item label="奖励内容">
                    <el-input v-model="form.Title"></el-input>
                </el-form-item>
                <el-form-item label="奖励类型">
                    <el-select v-model.number="form.AwardType" placeholder="请选择">
                        <el-option v-for="item in AwardTypeItem" :label="item.name" :key="item.value" :value="item.value"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="金额">
                    <el-input-number class="loose-input" v-model="form.Gold" :step="1" :min="0" ></el-input-number>
                </el-form-item>
                <el-form-item label="剩余数">
                    <el-input-number class="loose-input" v-model="form.RemainCount" :step="1" :min="0" ></el-input-number>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editVisible = false">取 消</el-button>
                <el-button type="primary" @click="saveEdit">确 定</el-button>
            </span>
        </el-dialog>

    </div>
    
</template>

<script>
    import {request} from '../../utils/http';
    export default {
        name: 'SpinWin',
        data() {
            return {
                AwardTypeItem:[{name:'金币',value:1},{name:'积分',value:2}],
                query: {
                    address: '',
                    name: '',
                    pageIndex: 1,
                    pageSize: 10
                },
                tableData: [],
                multipleSelection: [],
                delList: [],
                editVisible: false,
                pageTotal: 0,
                form: {},
                idx: -1,
                id: -1
            };
        },
        created() {
            this.getData();
        },
        methods: {
            // 获取 easy-mock 的模拟数据
            getData() {
                request({ url:'filmbll/getyhspinwinitemlist',  method:'get' }).then((res)=>{
                    console.log(res);
                    if (res.code==200){
                        this.tableData = res.obj;
                    }
                });
            },
            // 触发搜索按钮
            handleSearch() {
                this.getData();
            },
            // 编辑操作
            handleEdit(index, row) {
                this.idx = index;
                this.form = row;
                this.editVisible = true;
            },
            // 保存编辑
            saveEdit() {
                var data = {
                    jsonData:JSON.stringify(this.form)
                }
                request({
                    url:'filmbll/updateyhspinwinitem', method: 'post',
                    data:data
                }).then((res)=>{
                    console.log(res);
                    if (res.code==200){
                        this.editVisible = false;
                        this.$message.success(`修改第 ${this.idx + 1} 行成功`);
                        this.$set(this.tableData, this.idx, this.form);
                    } else {
                        this.$message.error('更新失败：'+res.msg);
                    }
                });
            }
        }
    };
</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px;
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .table {
        width: 100%;
        font-size: 14px;
    }
    .red {
        color: #ff0000;
    }
    .mr10 {
        margin-right: 10px;
    }
    .table-td-thumb {
        display: block;
        margin: auto;
        width: 40px;
        height: 40px;
    }
</style>
